
















/* eslint-disable */
import * as d3 from 'd3'
import * as utils from '@/assets/ts/c1.ts'
import { Component, Vue, Watch } from 'vue-property-decorator'
@Component({name: 'C1'})
export default class C1 extends Vue {
  private radio:string = '1'
  @Watch('radio', { immediate: true, deep: true })
  radioChanged (): void {
    utils.changeMode()
  }
  mounted (): void {
    d3.csv('./data/temperature_daily.csv').then((data: any) => utils.init(data, 'heatmap'))
  }
}

